import React, { Component } from 'react';
import '../admin.css';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import EditorWrap from '../../components/material/Editer/editorwrap'
import NavDropdown from 'react-bootstrap/NavDropdown';
import Media_img from '../../components/material/Editer/Add_media';
import {action_create_or_edit_page,get_page_info} from '../components_admin/lib/axios';
import { toast } from 'react-toastify';

Array.prototype.move = function(from,to){
  this.splice(to,0,this.splice(from,1)[0]);
  return this;
};
export default class Editer_page extends Component {
  constructor (props) {
    super(props)
    this.state = {
      post_selected:{
        type:-1,
        id:-1,
        title:'',
        img:''
      },
      type:"create",// create. edit. coppy
      // is_edit_slide:false,
      is_open_media:false,
      img_ref:'',
      triger_editer:0,
      data_in:{
        is_open:false,
        type_editer:'',
        des:''
      },
      hiden_long_des:true,
      data_sort:[],
      data:{
        status:false,//
        thumnail:' ',
        title:'',//
        short_des:'',
        long_des:''//
      },
    

    }
  }
  //
  async componentDidMount(){
    let {type}=this.state;
    if(type!="create"){
      let data =await get_page_info(this.state.post_selected.id)
      if(data.status!=undefined){
        this.setState({data:data})
      }
    }
  }
  //
  static getDerivedStateFromProps(nextProps, prevState){
    if(nextProps.trigerE!==prevState.trigerE){
      if(nextProps.post_selected.type==="create"){
        return {
          trigerE:nextProps.trigerE,
          type:nextProps.post_selected.type,
        }
      }else{
        return {
          trigerE:nextProps.trigerE,
          type:nextProps.post_selected.type,
          post_selected:nextProps.post_selected,
        }
      }
   }
    return null;
  }
  //
  fs_create_update_post=async ()=>{
    let {data,type,post_selected}=this.state;
    let id=type=="edit"?post_selected.id:-1;
    let a=await action_create_or_edit_page({
      idN:id,
      titleS:data.title,
      contentS:data.long_des,
      statusS:data.status?'publish':'private',
      metaA:{
          metaA:JSON.stringify({ // bien chung, gop bien o day
              thumnail:data.thumnail,
              short_des:data.short_des,
              titleS:data.title,
              statusS:data.status?'publish':'private',
          }),
          // bien can tao meta rieng o day
      },
    });
    if(a.id!=undefined){
      toast.success(type=="edit"?'Chỉnh sửa thành công':'Tạo mới thành công',{theme: "colored"})
      this.props.dataUpdate_list_posts(a);
    }else{
      toast.error(type=="edit"?'Chỉnh sửa thất bại':'Tạo mới thất bại',{theme: "colored"})
    }



  }
  //
 
  render() {
    let {data,hiden_long_des,triger_editer,data_in,is_open_media,type}=this.state;
    return (
      <React.Fragment>     
        <div className='container controlz marb'>
          <h1>{type=="edit"?"Chỉnh sửa bài viết":"Tạo bài viết mới"}</h1>
 
            <div className='wrap-s'>
              <div className='row'>
                <div className='col-6'>
                  <Form.Label htmlFor="basic-url"> <span style={{color:"red"}}>Công Khai</span> hay <span style={{color:"red"}}>Riêng Tư</span> bài viết này:</Form.Label>
                    <NavDropdown
                      id="nav-dropdown-dark-example"
                      title={!data.status?'Riêng tư':'Công khai'}
                      menuVariant="dark"
                    >
                        <NavDropdown.Item
                          onClick={()=>{
                            let {data}=this.state;
                            data.status=true;
                            this.setState({data:data})
                          }}
                        >Công khai</NavDropdown.Item>
                        <NavDropdown.Item
                          onClick={()=>{
                            let {data}=this.state;
                            data.status=false;
                            this.setState({data:data})
                          }}
                        >Riêng tư</NavDropdown.Item>
                  </NavDropdown>
                </div>
              </div>

             
            </div>
            <div className='wrap-s'>
              <Form.Label htmlFor="basic-url">Tiêu đề bài viết:</Form.Label>
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon3">
                  Tiêu đề
                </InputGroup.Text>
                <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                  value={data.title}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.title=e.target.value
                    this.setState({data:data})
                  }}
                />
              </InputGroup>
            </div>
            <div className='row'>
 
              <div className='col-6'>
                <div className='wrap-s'>
                  <Form.Label htmlFor="basic-url">Hình đại diện sản phẩm:</Form.Label>
                  <InputGroup className="mb-3">
                    <button className='buzz'
                      onClick={()=>{
                        this.setState({
                          img_ref:'thumnail',
                          is_open_media:true
                        })
                      }}
                    >
                      <i className="fa-solid fa-photo-film"></i> <span>Add Media</span>
                    </button>
                  </InputGroup>
                  <div className='thunaz'>
                    <img src={data.thumnail} height="80px"/>
                  </div>
                </div>
              </div>
            </div>
            <div className='wrap-s'>
              <Form.Label htmlFor="basic-url">Mô tả ngắn cho sản phẩm:</Form.Label>
              <FloatingLabel controlId="floatingTextarea2" label="Mô tả ngắn...">
                <Form.Control
                  as="textarea"
                  placeholder="Mô tả ngắn ..."
                  style={{ height: '100px' }}
                  value={data.short_des}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.short_des=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </FloatingLabel>
            </div>
            <div className='wrap-s'>
              <Form.Label htmlFor="basic-url">Tiêu đề bài viết: 
              <span className='eidtsz'
                onClick={()=>{
                  this.setState({
                    triger_editer:triger_editer+1,
                    data_in:{
                      is_open:true,
                      type_editer:'html',
                      des:'Chỉnh sửa nội dung chính của bài viết'}})
                }}
                >Chỉnh sửa</span>
                <span className='showz'
                    onClick={()=>this.setState({hiden_long_des:!hiden_long_des})}
                  >View</span>
              </Form.Label>
              <div className='container rvert'>
                  <section className='contents'>
                    <div className={hiden_long_des?'long-des lgk':'long-des' }  dangerouslySetInnerHTML={{__html:data.long_des}}>
                    </div>
                  </section>
              </div>
            </div>
        </div>
        <EditorWrap
          triger={triger_editer} 
          content={data.long_des}
          data_in={data_in}
          fs_close={()=>{
            this.setState({data_in:{is_open:false, type_editer:'', des:''}})
          }}
          fsReturn_editer={(long_des)=>{
            let {data}=this.state;
            data.long_des=long_des
            this.setState({data:data})
          }}
        />
        <Media_img 
          is_open_media={is_open_media}
          handleClose={()=>this.setState({is_open_media:false})}
          fsReturn_media={(img_return)=>{
            let {img_ref,data}=this.state;
            switch(img_ref){
              case 'thumnail':
                data.thumnail=img_return.img;
                this.setState({data:data})
                break;
            }         
          }}
        />
        <div className='footer-edit'>
            <Button variant="secondary" 
                onClick={()=>this.props.closer_editer()}
            >
                Hủy
            </Button>
            <Button variant="primary" 
              onClick={()=>this.fs_create_update_post()}
            >
                {type=="edit"?"Cập nhật bài viết":"Tạo bài viết mới"}
            </Button>
        </div>
      </React.Fragment>
    );
  }
  fs_rs_slide_sort=(data_sort)=>{
    let rs=[];
    data_sort.forEach(e => {
      rs.push(e.id)
    });
    return rs;
  }
}

