import React, { Component } from 'react';
import './admin.css';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Table from 'react-bootstrap/Table';
import Editer_page from './components_admin/Editer_page';
import { toast } from 'react-toastify';
import {get_pages,remove_post_by_id} from '../admin/components_admin/lib/axios'
const cz=25;
export default class Pages extends Component {
  constructor (props) {
    super(props)
    this.state = {
      page:0,
      show_read_more:true,
      is_open_modal_delete:false,
      trigerE:0,
      is_edit:false,
      post_selected:{
        type:-1,
        id:-1,
        title:'',
        img:''
      },
      posts:[
        // {
        //   url:"#",
        //   img:'https://anbinhnew.com/wp-content/uploads/2021/01/Giuong-sat-don-Hoang-Gia-mau-HG02-300x300.jpg',
        //   title:'Giường sắt giá rẻ -hộp vuông giảm giá ưu đãi khuyến mãi cao',
        //   categorys:[
        //     {
        //       name:'Giường sắt',
        //       url:'#'
        //     },
        //     {
        //       name:'Giường ngủ',
        //       url:'#'
        //     },
        //     {
        //       name:'Nội thất phòng ngủ',
        //       url:'#'
        //     }
        //   ],
        //   status:'true',
        //   id:'0',
        //   price:'2600000'
        // },
      ],

    }
  }
  async componentDidMount(){
    let {show_read_more}=this.state;
    let a= await get_pages(0);
    if(a.length<cz) show_read_more=false;
    this.setState({posts:a, show_read_more:show_read_more})
  }
  handle_close_modal_delete=()=>this.setState({is_open_modal_delete:false});
  render() {
      let {categorys,is_open_modal_delete,posts,post_selected,is_edit,trigerE,show_read_more}=this.state;
    return (
      <React.Fragment>
 

        {!is_edit&&<div>
          <div className='container controlz'>
            <div className='row controlk'>
              <div className='col-3'>
                <button type="button" className="btn btn-primary"
                   onClick={()=>{
                    this.setState({
                      trigerE:trigerE+1,
                      is_edit:true,
                      post_selected:{
                        type:'create',
                        title:'',
                        id:-1,
                        img:''
                      }
                    })
                  }}
                ><i className="fa-solid fa-plus"></i><span className="hgfs">Tạo mới</span></button>
              </div>
              <div className='col-6'></div>
      
            </div>
            <div className='row'>
              <Table striped bordered hover >
                <thead>
                  <tr>
                    <th style={{width:'1%'}}>#</th>
                    <th style={{width:'40%'}}>Sản phẩm</th>
                    <th style={{width:'8%'}}>Trạng thái</th>
                    <th style={{width:'20%'}}>Điều chỉnh</th>
                  </tr>
                </thead>
                <tbody>
                  {
                      posts.map((item,i)=>{
                        return(
                          <tr key={item.id}>
                            <td>
                                <input type="checkbox" value="" />
                            </td>
                            <td >
                              <div className='oiu'>
                                <div className='wlo'>
                                  <img src={item.img} className='imgxd'/>
                                </div>
                                <div className='wro'>
                                  <a target="_blank" href={item.url} className='afred'>{item.title}</a>
                                </div>
                              </div>
                            </td>
                            <td>
                              {item.status=="private"&&<span className='private'>Riêng tư</span>}
                              {item.status=="publish"&&<span className='publish'>Công khai</span>}
                            </td>
                            <td>
                              <div className='controlh'>
                                <div className='delete iconh'
                                  onClick={()=>this.setState({
                                    is_open_modal_delete:true,
                                    post_selected:{
                                      title:item.title,
                                      id:item.id,
                                      img:item.img
                                    }
                                  })}
                                ><i className="fa-solid fa-trash-can"></i><span>Xóa</span></div>
                                <div className='edit iconh'
                                  onClick={()=>{
                                    this.setState({
                                      trigerE:trigerE+1,
                                      is_edit:true,
                                      post_selected:{
                                        type:'edit',
                                        title:item.title,
                                        id:item.id,
                                        img:item.img
                                      }
                                    })
                                  }}
                                ><i className="fa-solid fa-pen-to-square"></i><span>Edit</span></div>
                              </div>
                            </td>
                          </tr>
                        )
                      })
                  }

                </tbody>
              </Table>
              {show_read_more&&<div className="frame"><button className="custom-btn btn-5"
                onClick={async ()=>{
                  let {page,posts,show_read_more}=this.state;
                  let a=[];
                   a= await get_pages(page+1);
                  if(a.length<cz) show_read_more=false;
                  posts=[...posts,...a]
                  this.setState({posts:posts,page:page+1,show_read_more:show_read_more})
                }}
              ><span>Xem thêm</span></button></div>}
            </div>
          </div>
          {/* Modal delete */}
          <Modal show={is_open_modal_delete} onHide={this.handle_close_modal_delete}>
            <Modal.Header closeButton>
              <Modal.Title>Xóa bài viết ({post_selected.id}):</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <img src={post_selected.img} width="30%" style={{display:'block',margin:'auto'}}/>
              <br/>
              {post_selected.title}
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={this.handle_close_modal_delete}>
                Hủy
              </Button>
              <Button variant="primary" onClick={async ()=>{
                let a=await remove_post_by_id(post_selected.id);
                if(a){
                  toast.success(`Xóa thành công.`,{theme: "colored"})
                  let {posts}=this.state;
                  let index=-1;
                  posts.forEach((e,i) => {
                    if(e.id===post_selected.id) index=i;
                  });
                  if(index!=-1){
                    posts.splice(index,1);
                    this.setState({posts:posts,is_open_modal_delete:false,post_selected:{id:-1,title:'',img:''}})
                  }

                }else{
                  toast.error('Xóa không thành công.',{theme: "colored"})
                }
              }}>
                Xác nhận xóa
              </Button>
            </Modal.Footer>
          </Modal>
        </div>}
        {is_edit&&<Editer_page
          categorys={categorys}
          trigerE={trigerE}
          post_selected={post_selected}
          closer_editer={()=>this.setState({is_edit:false})}
          dataUpdate_list_posts={(a)=>{
            let {posts}=this.state;
            let index=-1;
            posts.forEach((e,i) => {
              if(e.id==a.id) index=i; 
            });
            if(index!=-1){
                posts[index]=a;
            }else{
                posts.unshift(a);
            }
            this.setState({posts:posts,is_edit:false})
          }}
        />}
      </React.Fragment>
    );
  }
}

