import React, { Component } from 'react';
import './admin.css';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Table from 'react-bootstrap/Table';
import Editer_category from './components_admin/Editer_category';
import { toast } from 'react-toastify';
import {get_categorys,remove_cate_by_id} from '../admin/components_admin/lib/axios'
const cz=25;
export default class Categorys extends Component {
  constructor (props) {
    super(props)
    this.state = {
      is_open_modal_delete:false,
      trigerE:0,
      is_edit:false,
      post_selected:{
        type:-1,
        id:-1,
        title:'',
        img:''
      },
      categorys:[],
 

    }
  }
  async componentDidMount(){
    let categorys=await get_categorys();// [todo]
    this.setState({categorys:categorys })
  }
  handle_close_modal_delete=()=>this.setState({is_open_modal_delete:false});
  render() {
      let {categorys,is_open_modal_delete,post_selected,is_edit,trigerE}=this.state;
    return (
      <React.Fragment>
 

        {!is_edit&&<div>
          <div className='container controlz'>
            <div className='row controlk'>
              <div className='col-3'>
                <button type="button" className="btn btn-primary"
                   onClick={()=>{
                    this.setState({
                      trigerE:trigerE+1,
                      is_edit:true,
                      post_selected:{
                        type:'create',
                        title:'',
                        id:-1,
                        img:''
                      }
                    })
                  }}
                ><i className="fa-solid fa-plus"></i><span className="hgfs">Tạo mới</span></button>
              </div>
              <div className='col-6'></div>
            </div>
            <div className='row'>
              <Table striped bordered hover >
                <thead>
                  <tr>
                    <th style={{width:'1%'}}>#</th>
                    <th style={{width:'40%'}}>Danh mục</th>
                    <th style={{width:'8%'}}>Giá</th>
                    <th style={{width:'20%'}}>Điều chỉnh</th>
                  </tr>
                </thead>
                <tbody>
                  {
                      categorys.map((item,i)=>{
                        return(
                          <tr key={item.id}>
                            <td>
                                <input type="checkbox" value="" />
                            </td>
                            <td >
                              <div className='oiu'>
                                <div className='wlo'>
                                  <img src={item.img} className='imgxd'/>
                                </div>
                                <div className='wro'>
                                  <a target="_blank" href={item.url} className='afred'>{item.name}</a>
                                </div>
                              </div>
                            </td>
                            <td>
                              <span style={{fontSize:"12px"}}>
                                {Number(item.price).format(0, 3, '.', ',')} đ
                              </span>
                            </td>
                            <td>
                              <div className='controlh'>
                                <div className='delete iconh'
                                  onClick={()=>this.setState({
                                    is_open_modal_delete:true,
                                    post_selected:{
                                      title:item.name,
                                      id:item.id,
                                      img:item.img
                                    }
                                  })}
                                ><i className="fa-solid fa-trash-can"></i><span>Xóa</span></div>
 
                                <div className='edit iconh'
                                  onClick={()=>{
                                    this.setState({
                                      trigerE:trigerE+1,
                                      is_edit:true,
                                      post_selected:{
                                        type:'edit',
                                        title:item.name,
                                        id:item.id,
                                        img:item.img
                                      }
                                    })
                                  }}
                                ><i className="fa-solid fa-pen-to-square"></i><span>Edit</span></div>
                              </div>
                            </td>
                          </tr>
                        )
                      })
                  }

                </tbody>
              </Table>
            </div>
          </div>
          {/* Modal delete */}
          <Modal show={is_open_modal_delete} onHide={this.handle_close_modal_delete}>
            <Modal.Header closeButton>
              <Modal.Title>Xóa bài viết ({post_selected.id}):</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <img src={post_selected.img} width="30%" style={{display:'block',margin:'auto'}}/>
              <br/>
              {post_selected.title}
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={this.handle_close_modal_delete}>
                Hủy
              </Button>
              <Button variant="primary" onClick={async ()=>{
                let a=await remove_cate_by_id(post_selected.id);
                if(a){
                  toast.success(`Xóa thành công.`,{theme: "colored"})
                  let {posts}=this.state;
                  let index=-1;
                  categorys.forEach((e,i) => {
                    if(e.id===post_selected.id) index=i;
                  });
                  if(index!=-1){
                    categorys.splice(index,1);
                    this.setState({categorys:categorys,is_open_modal_delete:false,post_selected:{id:-1,title:'',img:''}})
                  }

                }else{
                  toast.error('Xóa không thành công.',{theme: "colored"})
                }
              }}>
                Xác nhận xóa
              </Button>
            </Modal.Footer>
          </Modal>
        </div>}
        {is_edit&&<Editer_category
          categorys={categorys}
          trigerE={trigerE}
          post_selected={post_selected}
          closer_editer={()=>this.setState({is_edit:false})}
          dataUpdate_list_posts={(a)=>{
            let {categorys}=this.state;
            let index=-1;
            categorys.forEach((e,i) => {
              if(e.id==a.id) index=i; 
            });
            if(index!=-1){
                categorys[index]=a;
            }else{
                categorys.unshift(a);
            }
            this.setState({categorys:categorys,is_edit:false})
          }}
        />}
      </React.Fragment>
    );
  }
}

