import React, { Component } from 'react';
import Modal from 'react-bootstrap/Modal'
import './Modal_checkout.css'
import {validate_phone,get_icon} from '../../../lib/fs'
// import {_order,_telegram} from './axios'
const iconz=<svg  className='selected-indicator '  xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
<g fill="none" fillRule="evenodd">
  <g>
    <g>
      <g>
        <g>
          <g>
            <g>
              <g>
                <path fill="#0D5CB6" d="M0 0h16c2.21 0 4 1.79 4 4v16L0 0z" transform="translate(-804 -366) translate(180 144) translate(484 114) translate(16 80) translate(0 28) translate(124)" />
                <g fill="#FFF">
                  <path d="M4.654 7.571L8.88 3.176c.22-.228.582-.235.81-.016.229.22.236.582.017.81L5.04 8.825c-.108.113-.258.176-.413.176-.176 0-.33-.076-.438-.203L2.136 6.37c-.205-.241-.175-.603.067-.808.242-.204.603-.174.808.068L4.654 7.57z" transform="translate(-804 -366) translate(180 144) translate(484 114) translate(16 80) translate(0 28) translate(124) translate(7.5)" />
                </g>
              </g>
            </g>
          </g>
        </g>
      </g>
    </g>
  </g>
</g>
</svg>

export default class Modal_checkout extends Component {
  constructor (props) {
    super(props)
    this.state = {
      validate_list:{
        list:[true,true,true,true],
        message:''
      },
      data_checkout:{
        img:'',
        msp:'',
        index:0,
        title:''
      },
      selected_kt:{
        kt:'',
        g:''
      },
      sl:1,
      sp:{},
      is_open:false,
      kh_name:'',
      kh_phone:'',
      kh_adress:'',
      kh_note:'',
      loading_status:0
    }
  }
  //
  async componentWillReceiveProps(nextProps){
    if(nextProps.a!==this.props.a){
      let {data_checkout,sp}=nextProps
      console.log("🚀 ~ file: Modal_checkout.js ~ line 45 ~ Modal_checkout ~ componentWillReceiveProps ~ data_checkout", data_checkout)
      console.log("🚀 ~ file: Modal_checkout.js ~ line 45 ~ Modal_checkout ~ componentWillReceiveProps ~ sp", sp)
      let selected_kt={kt:'',g:''};
      if(sp.table_price.length>0){
        selected_kt=sp.table_price[0]
      }
      this.setState({
        data_checkout:data_checkout,
        sp:sp,
        is_open:true,
        selected_kt:selected_kt
      })
    }
  }
  // dat hang
  action_dat_hang=async()=>{
    let {data_checkout,selected_kt,sl,kh_name,kh_phone,kh_adress,kh_note}=this.state;

    let list=[true,true,true,true];
    let message=[];
    let status =true;
    if(kh_name==""||kh_name==" "||kh_name.length<2) {
      list[0]=false;
      message.push(<span className='ms-z'>*HỌ VÀ TÊN bạn không được để trống.</span>);
      status=false;
    }
    if(kh_phone==""||!validate_phone(kh_phone)) {
      list[1]=false;
      message.push(<span className='ms-z'>*SỐ ĐIỆN THOẠI không chính xác.</span>);
      status=false;
    }
    if(kh_adress==""||kh_adress.length<8) {
      list[2]=false;
      message.push(<span className='ms-z'>*ĐỊA CHỈ bạn cần điền thêm thông tin để chúng tôi giao hàng dễ hơn </span>);
      status=false;
    }
    if(!status){
      this.setState({
        validate_list:{
          list:list,
          message:message
        }
      })
    }else{
      this.setState({
        loading_status:1,
        validate_list:{
          list:[true,true,true,true],
          message:''
        }
      })
      console.log("🚀 ~ file: Modal_checkout.js ~ line 75 ~ Modal_checkout ~ action_dat_hang=async ~ kh_note", kh_note)
      console.log("🚀 ~ file: Modal_checkout.js ~ line 75 ~ Modal_checkout ~ action_dat_hang=async ~ kh_adress", kh_adress)
      console.log("🚀 ~ file: Modal_checkout.js ~ line 75 ~ Modal_checkout ~ action_dat_hang=async ~ kh_phone", kh_phone)
      console.log("🚀 ~ file: Modal_checkout.js ~ line 75 ~ Modal_checkout ~ action_dat_hang=async ~ kh_name", kh_name)
    //   // gui data here
    //   let data={
    //     sp:{
    //       name:data_modal_selected.sp.title,
    //       attributes_kt:data_modal_selected.value_selected.title,
    //       price:data_modal_selected.value_selected.price,
    //       attributes_ms:selected_attributes.product_attributes,
    //       url_img:selected_attributes.img_url,
    //       quantity:quantity,
    //       z_note:data_info.note,
    //       url_sp:window.location.href,
    //       // ma_sp:
    //     },
    //     user:{
    //       z_name:data_info.name,
    //       z_phone:data_info.phone,
    //       z_address:data_info.address,
    //     }
    //   }
    //   this.props.action_step_1_to_server()
    //   // senddata to sercer here
    //   let a=await _order(data);
    //   if(a){
    //     _telegram(data);
    //     this.props.action_step_2_to_server(data);
    //   }else{
    //     alert("Đặt hàng không thành công!")
    //   }
     await setTimeout(()=>{
      this.setState({
        loading_status:2,
      })
      },3000)      
    }
  }
  // 
  render() {
    let {is_open,data_checkout,sp,selected_kt,sl,validate_list,loading_status}=this.state;
    let table_price='';
    if(sp.table_price!==undefined){
      table_price=sp.table_price;
    }else{
      return ''
    }
    return (
      <React.Fragment>
        <Modal
          show={is_open}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          className='wrapz'
          onHide={()=>{
            if(loading_status!==1){
            this.setState({is_open:false})
            }
          }}
        >
          {loading_status===0&&<div>
              <div className='header-modalz'>
                  <span className='titlezz'>{sp.title}</span>
                  <span className='devvn-popup-close'
                   onClick={()=>this.setState({is_open:false})}
                  >X</span>
                  <span className='dong' 
                    onClick={()=>this.setState({is_open:false})}
                  >Đóng</span>
              </div>
              <div className='row'>
                <div className='col-xs-12 col-sm-12 col-lg-6'>
                  <div className='row'>
                    <div className='col-4'>
                      <div className='war'>
                        <img 
                          src={data_checkout.img}
                          className="img-thumbnail"/>
                        <div>
                          <p ><span style={{padding:"3px",fontSize: '12px'}}>Giá :</span><span className='prz'>
                            {Number(selected_kt.g).format(0, 3, '.', ',')}  đ
                            </span></p>
                        </div>
                        <div className="devvn_prod_variable"> 
                          <div className="quantity" > 
                          <span className="syx"
                            onClick={()=>this.setState({sl:sl-1>1?sl-1:1})}
                          >-</span>
                          <span id="quantity-one" style={{fontSize: '14px', padding: '1px 5px'}}>{sl}</span> <span className="syx" 
                            onClick={()=>this.setState({sl:sl+1})}
                          >+</span> </div> 
                        </div>
                      </div>
                    </div>
                    <div className='col-8'>

                       <div className='wabz'>
                         <p>*Lựa chọn kích thước: </p>
                         <div>
                            
                            {
                              table_price.map((item,i)=>{
                                return(
                                  <button
                                    className={selected_kt.kt===item.kt?"btnz z-active":"btnz"} 
                                    onClick={()=>this.setState({selected_kt:item,sl:1})}
                                    key={i}
                                    > 
                                    {item.kt}
                                    {iconz}
                                  </button>
                                )
                              })
                            }
                         </div>
                       </div>

                    </div>
                    <div className='total-price'>
                      <span>Tổng cộng:</span> <span style={{fontWeight:'600',color:'#d50000'}} >
                        {(Number(sl)*Number(selected_kt.g)).format(0, 3, '.', ',')} đ</span>
                    </div>
                  </div>
                </div>
                <div className='col-xs-12 col-sm-12 col-lg-6'>
                  <div className='row'>
                      <div className='wapz'>
                        <div className='popup-customer-info-title'>Thông tin người mua</div>
                        <div className='wapzz'>
                          <div className="popup-customer-info-group"> 
                            <div className="popup-customer-info-item-2"> 
                              <input type="text" 
                                className={`inp ${validate_list.list[0]?'':'wr'}`} 
                                // className="inp wr"
                                 placeholder="Họ và tên(bắt buộc)"
                                value={this.state.kh_name}
                                onChange={(e)=>{
                                  this.setState({kh_name:e.target.value})
                                }}
                              /> 
                            </div> 
                            <div className="popup-customer-info-item-2"> 
                              <input type="number"
                                // className="inp"
                                className={`inp ${validate_list.list[1]?'':'wr'}`}
                               placeholder="Số điện thoại(bắt buộc)"
                                value={this.state.kh_phone}
                                onChange={(e)=>{
                                  this.setState({kh_phone:e.target.value})
                                }}
                              /> 
                            </div> 
                          </div>
                        </div>
                        <div className='wapzz'>
                          <div className="popup-customer-info-group"> 
                            <div className="popup-customer-info-item-1"> 
                              <textarea  
                                // className="inp wr"
                                className={`inp ${validate_list.list[2]?'':'wr'}`} 
                               placeholder="Địa chỉ"
                                style={{ height: '72px', minHeight: 'inherit'}}
                                value={this.state.kh_adress}
                                onChange={(e)=>{
                                  this.setState({kh_adress:e.target.value})
                                }}
                                
                              ></textarea> 
                              </div>
                          </div>
                        </div>
                        <div className='wapzz'>
                          <div className="popup-customer-info-group"> 
                            <div className="popup-customer-info-item-1"> 
                              <textarea className="inp" placeholder="Ghi chú đơn hàng" style={{ height: '72px', minHeight: 'inherit'}}
                                value={this.state.kh_note}
                                onChange={(e)=>{
                                  this.setState({kh_note:e.target.value})
                                }}
                              ></textarea> 
                              </div>
                          </div>
                        </div>
                        <div className='wapzz'>
                          {validate_list.message}
                        </div>
                        <div className='wapzz'>
                          <div className="popup-customer-info-group"> 
                            <div className="popup-customer-info-item-1"> 
                              <button  type="button" className="devvn-order-btn"
                                onClick={()=>this.action_dat_hang()}
                              >Đặt hàng ngay</button>
                            </div>
                          </div>
                        </div>
                      </div>
                  </div>
                </div>
              </div>
          </div>}
          {loading_status===1&&<div className="load-4">
            <div className="ring-1"></div>
          </div>}
          {loading_status===2&&<div className='container'>
          <div className='row'>
            <div className='col-12' style={{padding:"0px"}}>
              <div style={{textAlign:"center"}}>
                <div className='w-icon'>
                  <span className='icon-checkz'>{get_icon('check','#33a938','62px')}</span>
                </div>
                <h2 style={{paddingTop:"25px"}}>Đặt hàng thành công!</h2>
              </div>
            </div>
            <div className='col-12'>
              <p style={{textAlign: 'left', marginBottom: '8px', color: 'currentColor',marginTop: '12px'}}>Thông tin đơn hàng :</p>
              <table style={{borderCollapse: 'collapse', width: "100%"}} className="tabz">
                 <thead> 
                    <tr style={{backgroundColor: 'burlywood'}}> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Tên sản phẩm</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Giá thành</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Số lượng</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Thành tiền</th>
                    </tr>
                  </thead>
                    <tbody> 
                      <tr>
                        <td data-column="Tên sản phẩm" style={{backgroundColor: 'white'}}><span style={{color: '#333',fontSize: '14px',backgroundColor: 'white',}}>{data_checkout.title}<span style={{marginBottom: '0px', display: 'block',}}> Kích thước : {selected_kt.kt}</span><span style={{marginBottom: '7px', display: 'block',}}> Mẫu : {data_checkout.msp}</span></span>
                        <div>
                          <img width="40px" src={data_checkout.img}/>
                        </div>
                        </td>
                        <td data-column="Giá" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>
                          {(Number(selected_kt.g)*Number(sl)).format(0, 3, '.', ',')}
                        </td>
                        <td data-column="Số lượng" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>{sl}</td>
                        <td data-column="Thành tiền" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>
                          {(Number(sl)*Number(selected_kt.g)).format(0, 3, '.', ',')}
                           đ</td>
                      </tr>
                      <tr> 
                        <td></td>
                        <td></td>
                        <td style={{fontWeight: '600',fontSize: '14px'}}>Tổng tiền: </td> 
                        <td style={{fontWeight: '600', color: 'blue',fontSize: '14px',}}>
                          {(Number(sl)*Number(selected_kt.g)).format(0, 3, '.', ',')}
                          đ</td>
                      </tr> 
                    </tbody>
              </table>
              <div className='showzs'>
                <p>Thông tin người nhận: </p>
                <p>Tên: <b>{this.state.kh_name}</b></p>
                <p>Địa chỉ: <b>{this.state.kh_adress}</b></p>
                <p>Điện thoại: <b>{this.state.kh_phone}</b></p>
                <p>Ghi chú: <b> {this.state.kh_note}</b></p>
              </div>
              <div class="footer-pp"> 
                <p class="bnt-home" onClick={()=>this.setState({is_open:false,loading_status:0})}>Đóng</p>
              </div>
            </div>
          </div>
        </div>}
        </Modal>
      </React.Fragment>
    );
  }
}

