import React, { Component } from 'react';
import './card_slide.css'
import Slide2 from './slide2';
import {get_star} from "../../../lib/fs"
import Accordion from 'react-bootstrap/Accordion';
import Table from 'react-bootstrap/Table';
import {phone_anime,phone_zalo} from '../../../lib/fs'
import Modal_checkout from './Modal_checkout';
export default class Card_slide extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data_checkout:{
        img:'',
        msp:'',
        index:0
      },
      a:0,
    }
  }
  render() {
    let phone_html= phone_anime('xxxxxxxxxx');
    let zalo_html=phone_zalo('zzzzzzzzzz');
    let {cart_sp}=this.props;
    let {data_checkout,a}=this.state;
    return (
      <React.Fragment>
        <div className='row blz'>

          {
            cart_sp.map((item1,i)=>{
              let show_price_des='';
              if(item1.table_price.length===1){
                show_price_des=<div>Sản phẩm có giá là <span className='prc'>{Number(item1.table_price[0].g).format(0, 3, '.', ',')} ₫</span>. </div>;
              }else if(item1.table_price.length>1){
                show_price_des=<div>Sản phẩm có giá từ <span className='prc'>{Number(item1.table_price[0].g).format(0, 3, '.', ',')} ₫</span> đến <span className='prc'>{Number(item1.table_price[item1.table_price.length-1].g).format(0, 3, '.', ',')} ₫</span>.
                </div>;
              }
              return (
                <div className='col-12 col-lg-6 card-z1 ' key={item1.id}>
                <div className='card-sp'>
                  <div className='title-sp'>
                      <h3 className='tag'>{item1.title}
                        {/* <span className='reprep'><i class="fa-solid fa-pen-to-square"
                          onClick={()=>this.props.fs_editer('title',i,item1.title)}
                        ></i></span> */}
                      </h3>
                  </div>
                  <div className='w-card'>
                    <div className='slider-sp'>
                      <Slide2 
                        imgs={item1.imgs}
                        chon_mau_nay={(img,msp)=>this.setState({
                          data_checkout:{
                            img:img,
                            msp:msp,
                            index:i,
                            title:item1.title
                          },
                          a:a+1
                      })}

                      />
                    </div>
                    <div className='wrap-content-slide'>
                      <div className='price'>
                        <div>Zalo: <a className='zaloc' href='https://zalo.me/0962858650'  target="_blank">{zalo_html} </a></div>
                        <div className='gtt'>
                          Hotline:  
                          <span className='delz'><a href='tel:0938991602'>
                            <div className='waviy'  dangerouslySetInnerHTML={{__html:phone_html}}></div>
                          </a></span>
                        </div>
                        <div className='reviewz'>
                          <div className='rvz'>
                            Đánh giá :<span> {item1.rate}/5</span>
                            {/* <span className='reprep' style={{fontSize:"26px"}}><i class="fa-solid fa-pen-to-square"
                              onClick={()=>this.props.fs_editer('rate',i,item1.rate)}
                            ></i></span> */}
                          </div>
                          <div>
                            { get_star(Number(item1.rate),18)}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className='mo-ta-sp-slide'>
                      <div className='short-des'>

                      {/* <span className='reprep' style={{fontSize:"26px",position:"relative"}}><i style={{top:"-5px",left:"-10px"}} class="fa-solid fa-pen-to-square"
                              onClick={()=>this.props.fs_editer('short_des',i,item1.short_des)}
                            ></i></span> */}
                        {show_price_des}
                        
                        {item1.short_des}

                        
                      </div>
                    </div>
                    <div className='desz'>
                        <Accordion >
                              <Accordion.Item eventKey={i+'0'}>
                                  <Accordion.Header> <a>* Bảng giá sản phẩm</a></Accordion.Header>
                                  <Accordion.Body>
                                    <Table striped bordered hover size="sm" className='bgsp' >
                                      <tbody>
                                        {
                                          item1.table_price.map((item2,j)=>{
                                            return(
                                              <tr key={j}>
                                                <td>{item2.kt}</td>
                                                <td><span className='insi'>{Number(item2.g).format(0, 3, '.', ',')} đ</span></td>
                                              </tr>
                                            )
                                          })
                                        }
                                      </tbody>
                                    </Table>
                                  </Accordion.Body>
                            </Accordion.Item>
                              <Accordion.Item eventKey={i+'1'}>
                                  <Accordion.Header> <a>* Thông tin về sản phẩm</a></Accordion.Header>
                                  <Accordion.Body className='pading0'>
                                  {/* <span className='reprep' style={{fontSize:"26px",position:"relative"}}><i style={{top:"-5px",right:"-10px"}} class="fa-solid fa-pen-to-square"
                                  onClick={()=>this.props.fs_editer('long_des',i,item1.long_des)}
                                  ></i></span> */}
                                      <div className='contents'  dangerouslySetInnerHTML={{__html: item1.long_des}}></div>
                                  </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </div>
                  </div>
                </div>
            </div>
              )
            })
          }
        </div>
        <Modal_checkout 
          data_checkout={data_checkout}
          a={a}
          sp={cart_sp[data_checkout.index]}
        />
      </React.Fragment>
    );
  }
  //

}

