const axios = require('axios');
const { toast } =require ('react-toastify');
var url_home=window.data;
let url_create_or_edit_post=url_home+'/wp-content/themes/danhdev_1/templates/ajax/admin-components/posts/create_edit_post.php'
export async function action_create_or_edit_post(data){
    let data_send=new FormData();
    Object.keys(data).forEach(function(key) {
        if(key=='categoryA'){
            if(data[key].length>0){
                data[key].forEach(e => {
                    data_send.append('categoryA[]',e);
                });
            }else{
                    data_send.append('categoryA','null');
            }
        }else if(key=='metaA'){
            if(Object.keys(data[key]).length>0){
                Object.keys(data[key]).forEach(function(key_meta) {
                    data_send.append('metaA['+key_meta+']',data[key][key_meta]);
                })
            }else{
                data_send.append('metaA','null');
            }
        }else{
            data_send.append(key,data[key]);
        }
    });
    //
    let response= axios.post(url_create_or_edit_post, 
        data_send
    )
    .then(function (response) {
        return response.data;
    })
    .catch(function (error) {
        console.log("🚀 ~ file: axios.js ~ line 44 ~ action_create_or_edit_post ~ error", error)
        return {}
    })
    return response;
}
