import React, { Component } from 'react';
import './admin.css'
// import NavDropdown from 'react-bootstrap/NavDropdown';
// import Table from 'react-bootstrap/Table';
import Posts from './posts'
import Pages from './pages'
import Categorys from './categorys'
import Setups from './setup'
import Orders from './orders'
export default class Admin extends Component {
  constructor (props) {
    super(props)
    this.state = {
      seleted:'orders'
    }
  }
  render() {
      let {seleted}=this.state;
    return (
      <React.Fragment>
        <div>
          <div className=''>
            <div className=' navbarz'>
              <ul className='menuz'>
                {/* <li onClick={()=>this.setState({seleted:'home'})}  className={seleted==='home'?'active':''}><i className="fa-solid fa-house-chimney"></i> <span className="hgfs"> Trang chủ</span></li> */}
                <li onClick={()=>this.setState({seleted:'orders'})}  className={seleted==='orders'?'active':''}><i className="fa-solid fa-cart-shopping"></i> <span className="hgfs"> Đơn hàng </span></li>
                <li onClick={()=>this.setState({seleted:'posts'})}  className={seleted==='posts'?'active':''}><i className="fa-solid fa-signs-post"></i> <span className="hgfs"> Bài viết</span></li>
                <li onClick={()=>this.setState({seleted:'categorys'})}  className={seleted==='categorys'?'active':''}><i className="fa-brands fa-battle-net"></i> <span className="hgfs"> Danh mục </span></li>
                <li onClick={()=>this.setState({seleted:'pages'})}  className={seleted==='pages'?'active':''}><i className="fa-solid fa-file-word"></i> <span className="hgfs"> Trang</span></li>
                <li onClick={()=>this.setState({seleted:'setups'})}  className={seleted==='setups'?'active':''}><i className="fa-solid fa-gear"></i> <span className="hgfs"> Cài đặt</span></li>
              </ul>
            </div>
            <div className=' csdf'>
              {seleted==='posts'&&<Posts />}
              {seleted==='pages'&&<Pages />}
              {seleted==='categorys'&&<Categorys />}
              {seleted==='setups'&&<Setups />}
              {seleted==='orders'&&<Orders />}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

