import React, { Component } from 'react';
import Card_slide from '../../components/material/contents_category/card_slide';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import EditorWrap from '../../components/material/Editer/editorwrap';
import {generate_id} from '../../lib/fs'
export default class Edit_slide extends Component {
  constructor (props) {
    super(props)
    this.state = {
      triger:-1,
      cart_sp:[],
      is_view_long_des:false,
      is_view:false,
      triger_editer:0,
      by_edit:{
        type:'',
        i:-1,
        content:''
      },
      data_in:{
        is_open:false,
        type_editer:'',
        des:''
      },
    }
  }
// componentDidMount(){
//   let {cart_sp}=this.state;
//   console.log("🚀 ~ file: edit_slide.js ~ line 33 ~ Edit_slide ~ componentDidMount ~ cart_sp", cart_sp)
//   this.setState({
//     cart_sp:this.props.cart_sp
//   })
// }
  static getDerivedStateFromProps(nextProps, prevState){
    if(nextProps.triger!==prevState.triger){
      return({
        triger:nextProps.triger,
        cart_sp:nextProps.cart_sp,
      })
    }
    return null;
  }
  render() {
      let {cart_sp,triger_editer,data_in,by_edit,is_view,is_view_long_des}=this.state;
    return (
      <React.Fragment>

        <Modal show={this.props.is_open_edit_slider} size="xl">
            <Modal.Header closeButton>
              <Modal.Title>Chỉnh Slide 
              <span className='eidtsz fgt'
                 onClick={()=>this.setState({is_view:false})}
                >Chỉnh sửa</span>
                <span className='showz fgt'
                  onClick={()=>this.setState({is_view:true})}
                  >View</span>
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className='wrasx'>
                {is_view&&<Card_slide 
                  cart_sp={cart_sp}
                />}

              {!is_view&&<div className='editer-slide'>
  {/* xxxxxx */}
              {
                cart_sp.map((item,i)=>{
                  return(<div className='wrap-s'key={item.id}>
                  <Form.Label htmlFor="basic-url">Sản phẩm số {i+1}:
                    <span className='vvd'
                      onClick={()=>{
                        let {cart_sp} =this.state;
                        cart_sp.splice(i,1);
                        this.setState({cart_sp:cart_sp})
                      }}
                    >Xóa</span>
                  </Form.Label>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      Tiêu đề :
                    </InputGroup.Text>
                    <Form.Control placeholder='Tiêu đề...'
                        value={item.title}
                        onChange={(e)=>{
                          let {cart_sp}=this.state;
                          cart_sp[i].title=e.target.value;
                          this.setState({cart_sp:cart_sp})
                        }}
                      />
                      <Form.Control placeholder='Số điểm đánh giá /5' type='number'
                          value={item.rate}
                          onChange={(e)=>{
                            let {cart_sp}=this.state;
                            cart_sp[i].rate=e.target.value;
                            this.setState({cart_sp:cart_sp})
                          }}
                      />
                  </InputGroup>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      Mô tả ngắn:
                    </InputGroup.Text>
                      <Form.Control
                        as="textarea"
                        placeholder="Mô tả ngắn..."
                        style={{ height: '60px' }}
                        value={item.short_des}
                        onChange={(e)=>{
                          let {cart_sp}=this.state;
                          cart_sp[i].short_des=e.target.value;
                          this.setState({cart_sp:cart_sp})
                        }}
                      />
                  </InputGroup>
                  <div className='row'>
                    <div className='col-5'>
                      <div className='long-des lgk' style={is_view_long_des?{height:"auto"}:{}}>
                        <h5> Mô tả chi tiết về sản phẩm :
                          <span className='eidtsz fgt'
                              onClick={()=>this.setState({
                                triger_editer:triger_editer+1,
                                by_edit:{
                                  type:'long_des',
                                  i:i,
                                  content:item.long_des
                                },
                                data_in:{
                                  is_open:true,
                                  type_editer:'html',
                                  des:'Mô tả về sản phẩm'
                                },
                              })}
                          >Chỉnh sửa</span>
                          <span className='showz fgt'
                                onClick={()=>this.setState({is_view_long_des:!is_view_long_des})}
                          >View</span>
                        </h5>
                        <div className='contents'  dangerouslySetInnerHTML={{__html: item.long_des}}></div>
                      </div>
                    </div>
                    <div className='col-7'>
                      <h5>Thông tin về kích thước: </h5>
                      <div>
                        {
                          item.table_price.map((itemz,j)=>{
                            return(<InputGroup className="mb-3" key={j}>
                              <Form.Control placeholder='Tiêu đề :" kích thước"' aria-describedby="basic-addon3" 
                                value={itemz.kt}
                                onChange={(e)=>{
                                  let {cart_sp}=this.state;
                                  cart_sp[i].table_price[j].kt=e.target.value
                                  this.setState({cart_sp:cart_sp})
                                }}
                              />
                              <Form.Control placeholder='Giá :' aria-describedby="basic-addon3" type='number'
                                  value={itemz.g}
                                  onChange={(e)=>{
                                    let {cart_sp}=this.state;
                                    cart_sp[i].table_price[j].g=e.target.value
                                    this.setState({cart_sp:cart_sp})
                                  }}
                              />
                              <span className='iconktg'>
                                <i className="fa-solid fa-circle-minus"
                                  onClick={()=>{
                                    let {cart_sp}=this.state;
                                    cart_sp[i].table_price.splice(j,1);
                                    this.setState({cart_sp:cart_sp})
                                  }}
                                ></i>
                              </span>
                            </InputGroup>)
                          })
                        }

                        <div className='add-zz'>
                          <i className="fa-solid fa-circle-plus"
                            onClick={()=>{
                                let {cart_sp}=this.state;
                                cart_sp[i].table_price.push({
                                  kt:'',
                                  g:0
                                })
                                this.setState({cart_sp:cart_sp})
                              }
                            }
                          ></i>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row' style={{marginTop:'10px'}}>

                    {
                      item.imgs.map((itemz,j)=>{
                          return(
                            <div className='col-2' style={{paddingRight:"12px"}} key={j}>
                              <div style={{marginBottom:'5px'}}>
                                <div className='iconktg'>
                                  <i className="fa-solid fa-photo-film" style={{float:"left"}}
                                    onClick={()=>this.setState({
                                      triger_editer:triger_editer+1,
                                      by_edit:{
                                        type:'media',
                                        i:i,
                                        j:j,
                                        content:''
                                      },
                                      data_in:{
                                        is_open:true,
                                        type_editer:'media',
                                        des:''
                                      },
                                    })}
                                  ></i>
                                  <i className="fa-solid fa-circle-minus" style={{float:"right"}}
                                    onClick={()=>{
                                      let {cart_sp}=this.state;
                                      cart_sp[i].imgs.splice(j,1);
                                      this.setState({cart_sp:cart_sp})
                                    }}
                                  
                                  ></i>
                                </div>
                                <img src={itemz.img_url} width="100%" />
                              </div>
                              <div>
                                  <Form.Control placeholder='Mã sp...'
                                      value={itemz.msp}
                                      onChange={(e)=>{
                                        let {cart_sp}=this.state;
                                        cart_sp[i].imgs[j].msp=e.target.value;
                                        this.setState({cart_sp:cart_sp})
                                      }}
                                    />
                                    <Form.Check 
                                      type="switch"
                                      label="Text or HTML (html)"
                                      checked={itemz.is_text}
                                      onChange={(e)=>{
                                        let {cart_sp}=this.state;
                                        cart_sp[i].imgs[j].is_text=!cart_sp[i].imgs[j].is_text;
                                        this.setState({cart_sp:cart_sp})
                                      }}
                                    />
                                <InputGroup className="mb-3">
                                  <Form.Control
                                  as="textarea"
                                  placeholder="Mô tả ngắn..."
                                  style={{ height: '60px' }}
                                  value={itemz.html_mess}
                                  onChange={(e)=>{
                                    let {cart_sp}=this.state;
                                    cart_sp[i].imgs[j].html_mess=e.target.value;
                                    this.setState({cart_sp:cart_sp})
                                  }}
                                />
                                </InputGroup>
          
                              </div>
                            </div>
                          )
                      })
                    }
                    <div className='add-zz'>
                          <i className="fa-solid fa-circle-plus"
                            onClick={()=>{
                              let {cart_sp}=this.state;
                              cart_sp[i].imgs.push({
                                  img_url:"",
                                  html_mess:'',
                                  is_text:false,
                                  msp:''
                              });
                              this.setState({cart_sp:cart_sp})
                            }}
                          ></i>
                        </div>
                  </div>
                </div>)
              })
              }
  {/* xxxxxx */}
              <div className='add-zz'>
                <i className="fa-solid fa-circle-plus"
                  onClick={()=>{
                    let {cart_sp}=this.state;
                    cart_sp.push({
                      id:generate_id(4),
                      title: '',
                      rate:'5',
                      short_des:'',
                      long_des:'',
                      table_price:[],
                      imgs:[]
                    });
                    this.setState({cart_sp:cart_sp})
                  }}
                ></i>
              </div>
              </div>}

              <EditorWrap
                triger={triger_editer} 
                content={by_edit.content}
                data_in={data_in}
                fs_close={()=>this.setState({data_in:{is_open:false, type_editer:'', des:''}})}
                fsReturn_editer={(data)=>{
                  let {by_edit,cart_sp}=this.state;
                    switch(by_edit.type){
                      case "long_des":
                      cart_sp[by_edit.i].long_des=data;
                      this.setState({cart_sp:cart_sp})
                      break;
                      case "media":
                      cart_sp[by_edit.i].imgs[by_edit.j].img_url=data.img;
                      this.setState({cart_sp:cart_sp})
                      break;
                      
                    
                    }                
                  }}
              />
            </div>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="primary" 
                onClick={()=>{
                  console.log("hahah")
                  this.props.fsReturnz(this.state.cart_sp)
                }}
              >
                Ok
              </Button>
            </Modal.Footer>
          </Modal>
      </React.Fragment>
    );
  }
}

